.back-to-top {
  cursor: pointer;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background-color: #0000;
  border: 3px solid #a259ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  transition: border-color .3s, transform .3s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 26px;
}

.back-to-top.visible {
  opacity: 1;
  visibility: visible;
}

.back-to-top:hover {
  border-color: #fff;
  transform: translateY(-4px);
}

.back-to-top svg {
  stroke: #a259ff;
  width: 40px;
  height: 40px;
}
